(function(angular) {
  'use strict';

  angular
    .module('medszretailer')
    .directive('medszSidenav', medszSidenav);

  /** @ngInject */
  function medszSidenav() {
    var directive = {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/components/sideNav/sideNav.html',
      transclude: true,
      controller: SideNavController,
      controllerAs: 'sn'
    };

    return directive;

    /** @ngInject */
    function SideNavController($rootScope, $localStorage, $scope, $log, $timeout, $window, $document, $state, $mdSidenav, $mdDialog) {
      var vm = this;
      var ele;
      var active = 'background-color: #25aae1;';
      var not_active = 'background-color: #676a6c;';
      var rootListener;
      var pharmacyget='';

      vm.dash = not_active;
      vm.order = not_active;
      vm.history = not_active;
      vm.summary = not_active;
      vm.otc = not_active;
      vm.profile = not_active;
      vm.loyalCust = not_active;
      vm.announceClick = announceClick;
      vm.close = close;

      vm.goTo = goTo;
      vm.profilePopoup = profilePopoup;
      vm.routeTo = routeTo;
      vm.profileOption = ['Profile', 'Logout'];
      init();
      function init() {
        vm.userInfo = $localStorage['Pharget'];
        $rootScope.userInfo = vm.userInfo;
      }

      function goTo(to) {
        setActive(to);
        //$state.go(to);
      }

      function announceClick(index, ev) {
        if(index == 0){
          vm.profilePopoup(ev);
        } else if(index == 1){
          $localStorage.$reset();
          $state.go('Login');
          $rootScope.showSideNav = false;
        }
      }

      function close(){
        // console.log('ksjsck');
        $mdSidenav('nav').close();
      }

      function runWhenRoute(){

      switch($rootScope.selectedMenu) {
        case 'Home':
          vm.dash = active;
          vm.order = not_active;
          vm.history = not_active;
          vm.summary = not_active;
          vm.otc = not_active;
          vm.loyalCust = not_active;
          vm.profile = not_active;
          break;
        case 'Order':
          vm.dash = not_active;
          vm.order = active;
          vm.history = not_active;
          vm.summary = not_active;
          vm.otc = not_active;
          vm.loyalCust = not_active;
          vm.profile = not_active;
          break;
        case 'History':
          vm.dash = not_active;
          vm.order = not_active;
          vm.history = active;
          vm.summary = not_active;
          vm.otc = not_active;
          vm.loyalCust = not_active;
          vm.profile = not_active;
          break;
        case 'Summary':
          vm.dash = not_active;
          vm.order = not_active;
          vm.history = not_active;
          vm.summary = active;
          vm.otc = not_active;
          vm.loyalCust = not_active;
          vm.profile = not_active;
          break;
        case 'Otc':
          vm.dash = not_active;
          vm.order = not_active;
          vm.history = not_active;
          vm.summary = not_active;
          vm.otc = active;
          vm.loyalCust = not_active;
          vm.profile = not_active;
          break;
        case 'loyalCust':
          vm.dash = not_active;
          vm.order = not_active;
          vm.history = not_active;
          vm.summary = not_active;
          vm.otc = not_active;
          vm.loyalCust = active;
          vm.profile = not_active;
          break;
        case 'loyalCust':
          vm.dash = not_active;
          vm.order = not_active;
          vm.history = not_active;
          vm.summary = not_active;
          vm.otc = not_active;
          vm.loyalCust = not_active;
          vm.profile = active;
          break;
      }
    }

      function setActive(which) {
        switch(which) {
          case 'dash':
            vm.dash = active;
            vm.order = not_active;
            vm.history = not_active;
            vm.summary = not_active;
            vm.otc = not_active;
            vm.loyalCust = not_active;
            vm.profile = not_active;
            break;
          case 'order':
            vm.dash = not_active;
            vm.order = active;
            vm.history = not_active;
            vm.summary = not_active;
            vm.otc = not_active;
            vm.loyalCust = not_active;
            vm.profile = not_active;
            break;
          case 'history':
            vm.dash = not_active;
            vm.order = not_active;
            vm.history = active;
            vm.summary = not_active;
            vm.otc = not_active;
            vm.loyalCust = not_active;
            vm.profile = not_active;
            break;
          case 'summary':
            vm.dash = not_active;
            vm.order = not_active;
            vm.history = not_active;
            vm.summary = active;
            vm.otc = not_active;
            vm.loyalCust = not_active;
            vm.profile = not_active;
            break;
          case 'otc':
            vm.dash = not_active;
            vm.order = not_active;
            vm.history = not_active;
            vm.summary = not_active;
            vm.otc = active;
            vm.loyalCust = not_active;
            vm.profile = not_active;
            break;
          case 'loyalCust':
            vm.dash = not_active;
            vm.order = not_active;
            vm.history = not_active;
            vm.summary = not_active;
            vm.otc = not_active;
            vm.loyalCust = active;
            vm.profile = not_active;
            break;
          case 'profile':
            vm.dash = not_active;
            vm.order = not_active;
            vm.history = not_active;
            vm.summary = not_active;
            vm.otc = not_active;
            vm.loyalCust = not_active;
            vm.profile = active;
            break;
        }
      }

      rootListener = $scope.$watch(function () {
        return $rootScope.selectedMenu.length;
      }, function(newVal, oldVal) {
        runWhenRoute();
      });

      function routeTo(state) {
        $state.go(state);
      }

      function profilePopoup(ev) {
        console.log(ev);
          $mdDialog.show({
            controller: DialogController,
            controllerAs: 'profile',
            templateUrl: 'app/components/popup.temp.html',
            parent: angular.element(document.body),
            targetEvent: ev,
            clickOutsideToClose:true
          })
              .then(function(answer) {
                $scope.status = 'You said the information was "' + answer + '".';
              }, function() {
                $scope.status = 'You cancelled the dialog.';
              });
        };

        function DialogController($scope, $mdDialog, $localStorage) {
          $scope.pharmacyget = $localStorage['Pharget'];
          console.log($scope.pharmacyget);
            $scope.hide = function() {
              $mdDialog.hide();
            };

            $scope.cancel = function() {
              $mdDialog.cancel();
            };

            $scope.answer = function(answer) {
              $mdDialog.hide(answer);
            };
          }

      /* Initialize display values */
      function garbageCollection() {
      }
    }
  }

})(angular);

