(function(angular) {
    'use strict';


    angular.module('medszretailer')
        .service('homeService', homeService);

    /** @ngInject */
    function homeService($q, $http, $localStorage, getData, postData) {
        var service = {};
        service.dashboard = {};
        service.total_amount = '';
        service.delivery_charge_y = '';
        service.delivery_charge_s = '';
        service.gst_tex = '';
        service.grandTotal = '';
        service.init = init;
        return service;

        function init(currDate){
        	if(typeof currDate != 'object') {
        		var currDate = new Date();
        	}
        	var deferred = $q.defer();
        	var url = 'https://www.yoopha.com/shim/order.php?actiontype=1&subtype=3&pharma_id=' + $localStorage['Pharmausr'].url_id + '&yyyy=' + currDate.getFullYear() + '&mm=' + (currDate.getMonth() + 1) + '&token=' + $localStorage['Pharmausr'].token;
        	getData.async(url)
        		.then(function(res){
        			// console.log(res);
        			service.dashboard = res.results;
              		calcTot(service.dashboard);
        			deferred.resolve(res);
            });
        	return deferred.promise;
        }

        function calcTot(data){
            for(var i = 0; i < data.length; i++){
                service.total_amount += parseFloat(data[i].total_amount);
                if(data[i].delivered_by == 'YOOPHA')
                    service.delivery_charge_y += parseFloat(data[i].service_charges);
                else
                    service.delivery_charge_s += parseFloat(data[i].service_charges);
            }
            var temp = service.delivery_charge_y + service.delivery_charge_s;
            service.gst_tex = (18 / 100) * temp;
            service.grandTotal = service.gst_tex + temp;
        }

    }
})(angular);
